  display: flex;
  align-items: center
}

.side-menu .side-menu-body {
  height: 100vh;
  overflow-y: scroll;
  transform: translateX(100%);
  transition: all .2s ease-in-out;
  pointer-events: all
}

.side-menu .btn {
  transition: all .2s ease-in-out;
  pointer-events: all;
  position: relative;
  margin-right: 1.5rem;
  left: calc(100% - 4.5rem)
}

.side-menu .btn .icon {
  margin: 0 !important;
  transition: all .2s ease-in-out
}

.side-menu .btn .icon:last-child {
  position: absolute;
  opacity: 0
}

.side-menu .btn.active {
  left: 0
}

.side-menu .btn.active .icon:first-child {
  opacity: 0
}

.side-menu .btn.active .icon:last-child {
  opacity: 1
}

.side-menu .btn.active+.side-menu-body {
  transform: translateX(0)
}

.side-menu .nav-link {
  color: #555a64
}

.side-menu .nav-link:not(.active),
.side-menu .nav-link:not(:hover) {
  opacity: .5
}

.side-menu .nav-link.active,
.side-menu .nav-link:hover {
  color: #009b72;
  opacity: 1
}

